<template>
  <router-view></router-view>
  <Footer />
</template>

<script>
import Footer from './components/Footer.vue'

export default {
  name: 'App',
  components: {
    Footer
  },
  created() {
    window.addEventListener('load', this.resizeBlock),
    window.addEventListener('scroll', this.handleScroll),
    window.addEventListener('resize', this.resizeBlock)
  },
  onUnmounted() {
    window.addEventListener('load', this.resizeBlock),
    window.addEventListener('scroll', this.handleScroll),
    window.addEventListener('resize', this.resizeBlock)
  },
  methods: {
    handleScroll() {
      let block = document.getElementById('filter')
      if (block && window.scrollY > 100 && window.innerWidth > 992) {
        block.classList.add('filter_fix')
      } else {
          if (block) {
            block.classList.remove('filter_fix')
          }
      }
    },
    resizeBlock() {
      let block = document.getElementById('filter')
      if (window.innerWidth < 992) {
        block.classList.add('offcanvas-collapse')
      } else {
          if (block) {
            block.classList.remove('offcanvas-collapse')
          }
      }
    }
  }
}
</script>


