<template>
<Header>
    <FilterIcon />
</Header>
<main>
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-4 col-lg-4">
          <Filter v-model:filter-price-from="filterPriceFrom"
                  v-model:filter-price-to="filterPriceTo"
                  v-model:filter-category-id="filterCategoryId"
                  v-model:filter-taste-id="filterTasteId" />
        </div>
        <div class="col-xs-12 col-sm-12 col-md-8 col-lg-8">
          <Catalog :products="products" :all-count-products="allCountProducts" :count-products="countProducts" />
        </div>
      </div>
    </div>
    <BasePagination v-model:current-page="currentPage" :size-page="sizePage" :count="countProducts" />
</main>
</template>

<script>
import Header from '../components/Header'
import FilterIcon from '../components/FilterIcon'
import Catalog from '../components/Catalog.vue'
import Filter from '../components/Filter.vue'
import BasePagination from '../components/BasePagination'
import products from '../data/products'

export default {
  name: 'MainPage',
  components: {
    Header,
    FilterIcon,
    Filter,
    Catalog,
    BasePagination
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterTasteId: [],
      currentPage: 1,
      sizePage: 6
    }
  },
  computed: {
    filteredProducts() {
      let filteredProducts = products;

      if(this.filterPriceFrom > 0) {
        filteredProducts = filteredProducts.filter(product => product.price >= this.filterPriceFrom);
      }

      if(this.filterPriceTo > 0) {
        filteredProducts = filteredProducts.filter(product => product.price <= this.filterPriceTo);
      }

      if(this.filterCategoryId > 0) {
        filteredProducts = filteredProducts.filter(product => product.categoryID === this.filterCategoryId);
      }

      if(this.filterTasteId.length !== 0) {
        filteredProducts = filteredProducts.filter(product => product.tasteID.some(item => this.filterTasteId.some(i => i === item)));
      }

      return filteredProducts;
    },
    products() {
      const offset = (this.currentPage - 1) * this.sizePage;
      return this.filteredProducts.slice(offset, offset + this.sizePage);
    },
    countProducts() {
      return this.filteredProducts.length;
    },
    allCountProducts() {
        return products.length;
    }
  }
}
</script>
