<template>
<Header />
<main>
  <div class="container">

    <div class="row">
        <div class="col-xs-12 col-sm-12 col-md-2 col-lg-2"></div>

        <div class="col-xs-12 col-sm-12 col-md-4 col-lg-4">
          <ProductItem :product="product" :taste-img="tasteImg" />
        </div>

        <div class="col-xs-12 col-sm-12 col-md-4 col-lg-4">
          <form class="order_form" @submit.prevent="addToCart">

            Select product taste: 
            <ul class="product_img_list">
              <li v-for="(taste, index) in productTaste" :key="index"
                  :class="{active: tasteIndex === index}">
              <label class="radio">
                <input type="radio" 
                       class="product_img_list__radio"
                       :name="product.id" 
                       :value="taste.id" 
                       v-model.number="productCheckedTaste"
                       :checked="checked(index)" 
                       @click="checkedTaste(index)" />
                <span class="label-body">
                  <img :src="product.images[index].img_path_png" :alt="taste.title" />
                  {{ taste.title }}
                </span>
              </label>
              </li>
            </ul>

            Select product count:
            <input type="number" 
                   placeholder="1" 
                   v-model.number="productCount" 
                   min="1" />

            <button type="submit" class="add_to_cart">add to cart</button>
          </form>
        </div>

    </div>

  </div>
</main>
</template>

<script>
import productTaste from '../mixins/productTaste'
import products from '../data/products'
import ProductItem from '../components/ProductItem'
import Header from '../components/Header'

export default {
    name: 'ProductPage',
    components: {
      ProductItem,
      Header
    },
    data() {
      return {
        productCount: 1,
        productCheckedTaste: null
      }
    },
    mixins: [productTaste],
    computed: {
        product() {
            return products.find(product => product.id === +this.$route.params.id);
        },
        tasteImg() {
          return this.product.images[this.tasteIndex];
        }
    },
    methods: {
        addToCart() {
            this.$store.commit(
                'addProductToCart', 
                { 
                  productId: this.product.id, 
                  productCount: this.productCount,
                  productCheckedTaste: this.productCheckedTaste ? this.productCheckedTaste : +this.productTaste[0].id,
                  tasteIndex: this.tasteIndex
                }
            )
        }
    }
}
</script>

