  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
  padding-bottom: 20px;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0px;
}
.product_img_list li {
  width: 25%;
  margin-right: 20px;
  border-radius: 2px;
  border: solid 1px #ccc;
}
.product_img_list li:hover,
.product_img_list li:active,
.product_img_list li:focus {
  border-color: #ff569b;
}
.product_img_list li.active {
  border-color: #ff569b;
}
.product_img_list__radio {
    display: none;
}
.radio {
  margin: 0;
  padding: 4px 10px;
  cursor: pointer;
}
</style>

