<template>
<div class="filter" id="filter">
  <h3>FILTER</h3>
  <form class="filter_form" @submit.prevent="submit">
    <fieldset>
      <h4>price</h4>
      <label>
        from
          <input type="number" 
                 name="price_from" 
                 placeholder="from" 
                 v-model.number="currentPriceFrom" 
                 min="0"/>
      </label>

      <label>
        to
        <input type="number" 
               name="price_to" 
               placeholder="to" 
               v-model.number="currentPriceTo" 
               min="0" />
      </label>
    </fieldset>

    <fieldset>
      <h4>categories</h4>
      <select v-model.number="currentCategoryId">
        <option value="0">all</option>
        <option :value="category.id" 
                v-for="category in categories" 
                :key="category.id">
          {{ category.title }}
        </option>
      </select>
    </fieldset>

    <fieldset>
        <h4>taste</h4>
        <ul class="filter_form__taste_list">
          <li v-for="taste in tastes" :key="taste.id">
            <label>
            <input type="checkbox" 
                   :value="taste.id" 
                   v-model="currentTasteId" /> 
              {{ taste.title }}
            </label>
          </li>
        </ul>
    </fieldset>

    <fieldset class="flex">
      <input class="filter_form__btn_reset" type="reset" value="reset" @click.prevent="reset" />  
      <input class="filter_form__btn_submit" type="submit" value="apply" />
    </fieldset>

    </form>
</div>
</template>

<script>
import categories from '../data/categories'
import tastes from '../data/tastes'

export default {
    name: 'Filter',
    props: ['filterPriceFrom', 'filterPriceTo', 'filterCategoryId', 'filterTasteId'],
    data() {
        return {
            currentPriceFrom: 0,
            currentPriceTo: 0,
            currentCategoryId: 0,
            currentTasteId: []
        }
    },
    watch: {
        filterPriceFrom(value) {
            this.currentPriceFrom = value;
        },
        filterPriceTo(value) {
            this.currentPriceTo = value;
        },
        filterCategoryId(value) {
            this.currentCategoryId = value;
        },
        filterTasteId(value) {
            this.currentTasteId = value;
        }
    },
    methods: {
        submit() {
            this.$emit('update:filterPriceFrom', this.currentPriceFrom);
            this.$emit('update:filterPriceTo', this.currentPriceTo);
            this.$emit('update:filterCategoryId', this.currentCategoryId);
            this.$emit('update:filterTasteId', this.currentTasteId);
        },
        reset() {
            this.$emit('update:filterPriceFrom', 0);
            this.$emit('update:filterPriceTo', 0);
            this.$emit('update:filterCategoryId', 0);
            this.$emit('update:filterTasteId', []);
        }
    },
    computed: {
        categories() {
            return categories;
        },
        tastes() {
            return tastes;
        }
    }
}
</script>

<style>
.flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}
.filter {
  min-height: 200px;
  padding: 10px;
  background-color: #222;
}
.filter label {
  color: #fff;
}
.filter h4 {
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    font-size: .9em;
}
.filter_fix {
  position: fixed;
  top: 0;
  width: 27%;
  max-width: 340px;
}
.filter_form > * {
  color: #fefefe;
}
.filter_form__taste_list {
    list-style: none;
    margin-block-start: 0;
    margin-block-end: 0;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 0px;
    column-width: 100px;
}
input[type="submit"].filter_form__btn_submit {
    width: 45%;
    background-color: #bcd35f;
    border-color: #bcd35f;
    color: #fefefe;
}
input[type="reset"].filter_form__btn_reset {
    width: 45%;
}
.error {
    border-color: red !important;
    color: red !important;
}
</style>