import { createStore } from 'vuex'
import products from '../data/products'

// Create a new store instance.
const store = createStore({
    state: {
        cartProducts: []
    },
    mutations: {
        addProductToCart(state, { productId, productCount, productCheckedTaste, tasteIndex }) {
            let item = state.cartProducts.find(item => item.productId === productId && item.productCheckedTaste === productCheckedTaste)
            if(item) {
                item.productCount += productCount;
            } else {
                state.cartProducts.push({
                    productId,
                    productCount,
                    productCheckedTaste,
                    tasteIndex
                })
            }
        },
        updateCartProductCount(state, { productId, productCount, productCheckedTaste }) {
            let item = state.cartProducts.find(item => item.productId === productId && item.productCheckedTaste === productCheckedTaste)
            if(item) {
                item.productCount = productCount;
            }
        },
        deleteCartProduct(state, { productId, productCheckedTaste }) {
            let itemDel = state.cartProducts.filter(item => item.productId === productId && item.productCheckedTaste === productCheckedTaste)
            state.cartProducts = state.cartProducts.filter(item => item !== itemDel[0])
        },
        clearCart(state) {
            return state.cartProducts = []
        }
    },
    getters: {
        cartDetailProducts(state) {
          return state.cartProducts.map(item => {
              let product = products.find(p => p.id === item.productId)
              return {
                  ...item,
                  product: product,
                  productTotalPrice: product.price * item.productCount
              }
          })
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.productCount) + acc, 0)
        },
        cartTotalCount(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => item.productCount + acc, 0)
        },
        cartTotalWeight(state, getters) {
            return getters.cartDetailProducts.reduce((acc, item) => (item.product.weight * item.productCount) + acc, 0)
        }
    }
  })

export default store;