<template>
<div class="pagination" v-if="count > sizePage">
  <button class="pagination__prev"
          :class="{'disabled' : currentPage <= 1}"
          @click.prevent="paginate(currentPage - 1, pages)">
    <div class="arrow_left"></div>
  </button>

  <button class="pagination__item" :class="{'active' :pageNumber === currentPage}"
          v-for="pageNumber in pages" :key="pageNumber"
          @click.prevent="paginate(pageNumber, pages)">
    {{ pageNumber }}
  </button>

  <button class="pagination__next"
          :class="{'disabled' : currentPage >= pages}"
          @click.prevent="paginate(currentPage + 1, pages)">
    <div class="arrow_right"></div>
  </button>
</div>
</template>

<script>
export default {
  name: 'BasePagination',
  model: {
    prop: 'currentPage',
    event: 'paginate',
  },
  props: ['currentPage', 'sizePage', 'count'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.sizePage);
    },
  },
  emits: ['update:currentPage'],
  methods: {
    paginate(currentPage, pages) {
      if (currentPage >= 1 && currentPage <= pages) {
        this.$emit('update:currentPage', currentPage);
      }
    },
  },
};
</script>

<style>
