  text-align: center;
  -webkit-transition : all 500ms ease-out;
  -moz-transition : all 500ms ease-out;
  -o-transition : all 500ms ease-out;
  transition : all 500ms ease-out;
}
.pagination > button:hover {
  background-color: #bcd35f;
  color: #fafafa;
  border: 1px solid #bcd35f;
}
.pagination > button.disabled {
  cursor: crosshair;
  color: #ccc;
}
.pagination > button.disabled:hover {
  border: 1px solid transparent;
  background-color: transparent;
  color: #ccc;
}
.pagination__item.active,
.pagination__item.active:hover {
  background-color: #bcd35f;
  color: #fafafa;
  border: 1px solid #bcd35f;
  cursor: crosshair;
}
.arrow_right {
  width: 16px;
  height: 16px;
  background-image: url('/img/svg/sprite.svg');
  background-repeat: no-repeat;
  background-position: -80px 0;
  background-size: cover;
}
.pagination__next:hover .arrow_right {
  background-position: -64px 0;
}
.pagination > button.pagination__next.disabled .arrow_right {
  background-position: -96px 0;
}
.arrow_left {
  width: 16px;
  height: 16px;
  background-image: url('/img/svg/sprite.svg');
  background-repeat: no-repeat;
  background-position: -80px 0;
  background-size: cover;
  transform: rotate(.5turn);
}
.pagination__prev:hover .arrow_left {
  background-position: -64px 0;
}
.pagination > button.pagination__prev.disabled .arrow_left {
  background-position: -96px 0;
}
</style>