<template>
    <button class="display-lg-none"
                @click="ToggleFilter">
          <span v-if="isOpen">close filter</span>
          <span v-else>open filter</span>
    </button>
</template>

<script>
export default {
    name: 'FilterIcon',
    data() {
      return {
        isOpen: false
      }
    },
    methods: {
      ToggleFilter() {
        document.getElementById('filter').classList.toggle('open'),
        this.isOpen = !this.isOpen
        if(this.isOpen) {
          document.body.style.overflowY = "hidden"
        } else {
          document.body.style.overflowY = "auto"
        }
      }
    }
}
</script>