<template>
<Header />
<main>
  <div class="container">
    <div class="row" v-if="products.length > 0">

      <div class="col-xs-12 col-sm-12 col-md-8 col-lg-8">
        <ul class="cart_product___list">
          <CartItem v-for="item in products" :key="item.id" :item="item" />
        </ul>
      </div>

      <div class="col-xs-12 col-sm-12 col-md-4 col-lg-4">
        <p class="cart_product__total_count">Total count: {{ totalCount }} unit</p>
        <p class="cart_product__total_weight">Total weight: {{ totalWeight }} gramm</p>
        <p class="cart_product__total_price">Total price: {{ totalPrice }} u.e.</p>
        
        <router-link :to="{name: 'order'}" >
        <button @click="clearCart" class="add_order">add order</button>
        </router-link>
      </div>
    </div>

    <div class="cart_empty" v-else>
      Cart is empty...
      <img :src="`${publicPath}img/svg/cart_empty.svg`" alt="Cart is empty" />
    </div>
    
  </div>
</main>
</template>

<script>
import Header from '../components/Header'
import { mapGetters, mapMutations } from 'vuex'
import CartItem from '../components/CartItem'

export default {
    name: 'CartPage',
    components: {
      CartItem,
      Header
    },
    data () {
      return {
        publicPath: process.env.BASE_URL
      }
    },
    computed: {
      ...mapGetters({
        products: 'cartDetailProducts',
        totalPrice: 'cartTotalPrice',
        totalCount: 'cartTotalCount',
        totalWeight: 'cartTotalWeight'
      }),
      ...mapMutations({
        clearCart: 'clearCart'
      })
    }
}
</script>

