<template>
<li class="col-xs-6 col-sm-6 col-md-4 col-lg-4">
  <div class="product_list__item">

  <router-link :to="{name: 'product', params: { id: product.id }}">
    <div class="product_list__item_title">
      <h4>{{ product.title }}</h4>
      <img :src="this.publicPath + tasteImg.img_path_png" :alt="product.title" />
    </div>
  </router-link>

  <p class="product_list__item_price">
    {{ product.price }} u.e.
  </p>
  <p class="product_list__item_weight">
    {{ product.weight }} gramm
  </p>
  <p class="product_list__item_attr">
    <span class="product_list__item_category">{{ productCategory.title }}</span>, 
    <span class="product_list__item_type">{{ productType.title }}</span>
  </p>
  
  <ul class="product_list__attr product_list__item_taste">
  <li v-for="(taste, index) in productTaste" :key="taste.id">
    <label class="radio">
      <input type="radio" 
             :name="product.id" 
             :value="taste.id" 
             :checked="checked(index)" 
             @click="checkedTaste(index)" />
      <span class="label-body"> {{ taste.title }}</span>
    </label>
  </li>
</ul>

  </div>
</li>
</template>

<script>
import productTaste from '../mixins/productTaste'
import productCategory from '../mixins/productCategory'
import productType from '../mixins/productType'

export default {
    name: 'CatalogItem',
    props: ['product'],
    mixins: [productTaste, productCategory, productType],
    data () {
      return {
        publicPath: process.env.BASE_URL
      }
    },
    computed: {
      tasteImg() {
        return this.product.images[this.tasteIndex];
      }
    }
}
</script>

