import tastes from '../data/tastes'

const productTaste = {
    data() {
      return {
        tasteIndex: 0
      }
    },
    computed: {
      productTaste() {
        return tastes.filter(productTaste => this.product.tasteID.some(i => i === productTaste.id))
      }
    },
    methods: {
      checked(index) {
        if(index === 0) {
          return true;
        } else {
          return false;
        }
      },
      checkedTaste(index) {
        return this.tasteIndex = index
      }
    }
}

export default productTaste;