<template>
<li class="cart_item flex">
  <div>
    <img :src="tasteImg.img_path_png"
         :alt="checkedTaste.title"
         class="cart_item__img" />
  </div>

  <div class="cart_item__attr">
    <h2>{{ item.product.title }}</h2>
    <p><small>weight:</small> {{ item.product.weight }} gramm, <small>taste:</small> {{ checkedTaste.title }}</p>
  </div>

  <div class="cart_item__count">
    <input type="number" 
           v-model.number="productCount" 
           min="1" />
  </div>

  <div>
   x {{ item.product.price }} u.e. = {{ item.productTotalPrice }} u.e.
  </div>

  <div>
    <button @click.prevent="deleteProduct"
            class="cart_item__delete_product"
            aria-label="delete product from cart">
      <div class="delete_product"></div>
    </button>
  </div>
</li>
</template>

<script>
import tastes from '../data/tastes'

export default {
    name: 'CartItem',
    props: ['item'],
    computed: {
        tasteImg() {
          const index = this.item.tasteIndex
          return this.item.product.images[index];
        },
        productCount: {
          get() {
            return this.item.productCount;
          },
          set(value) {
            this.$store.commit(
              'updateCartProductCount', { 
                productId: this.item.productId, 
                productCount: value, 
                productCheckedTaste: this.item.productCheckedTaste 
            })
          }
        },
        checkedTaste() {
          return tastes.find(checkedTaste => checkedTaste.id === this.item.productCheckedTaste)
        }
    },
    methods: {
      deleteProduct() {
          this.$store.commit(
            'deleteCartProduct', {
              productId: this.item.productId,
              productCheckedTaste: this.item.productCheckedTaste
          })
        }
    }
}
</script>

<style scoped>
.cart_item {
  border-bottom: 1px solid #efefef;
  padding: 10px 0;
}
.cart_item .flex {
  align-items: center;
}
.cart_item__attr {
  width: 40%;
}
.cart_item__delete_product {
  border: none;
  margin: 0;
}
.delete_product {
  width: 20px;
  height: 20px;
  background-image: url('/img/svg/sprite.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: -140px 0;
}
.cart_item__delete_product:hover .delete_product {
  background-position: -160px 0;
}
.cart_item__count input {
  margin: 0;
  width: 100px;
}
.cart_item__img {
  width: 100px;
  height: 100px;
}
</style>